* {
    margin: 0;
    padding: 0;
    font: 20px/1.2 "Roboto", sans-serif;
    color: #222;
}

.App {
    width: 1200px;
    margin: 10px auto;

    display: grid;
    grid-template-areas:
      "header header"
      "sidebar content"
      "footer footer";
    grid-template-rows: auto;
    grid-template-columns: 2fr 10fr;
    gap: 10px;
}
